import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {Alert} from 'reactstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button , Jumbotron} from 'reactstrap';
const Example = (props) => {
    return (
      <div>
        <Jumbotron>
          <h1 className="display-3">get news</h1>
          <p className="lead">get updated news every hour.</p>
          <hr className="my-2" />
          <p>let's go</p>
        
        </Jumbotron>
      </div>
    );
  };
  

var request = require('request');
var url = 'http://newsapi.org/v2/top-headlines?country=in&apiKey=d1f129f9dca845bf9578e7f7119d8586'

request(url,async (err, response, body)=>{
     var bool = true;
     var s = ""
    if(!err && response.statusCode === 200){
       const parsed_data = await JSON.parse(body);
        console.log(parsed_data.articles[0])
    const rend = parsed_data.articles.map((element, move)=>{
        if(bool){
          s = 'danger';
        }
        else{
            s = 'warning';
        }
        bool = !bool;
        return(
            <Alert color = {s} className = "board">
                <h1>({move+1}) {element.description}</h1>
                <img src = {element.urlToImage}/>
                </Alert>
        )
    })
ReactDOM.render(
        <div>
            <Example/>
            {rend}
        </div>,
    document.getElementById('root'))
    }
  })


